const S3_BUCKET = 'https://mrp-paintings.s3.us-east-2.amazonaws.com'

function Image({ title }) {
    const titleUrl = title.replaceAll(' ', '+')
    return (
        <div>
            <img src={`${S3_BUCKET}/${titleUrl}/small.jpg`} alt={title} />
            <p>{title}</p>
        </div>
    )
}

export default Image
